<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">required</code> The URL of the WMS service. The URL supports the same keywords as the UrlTemplateImageryProvider.</td>
</tr>
<tr>
<td>layers</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> The layers to include, separated by commas.</td>
</tr>
<tr>
<td>parameters</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Additional parameters to pass to the WMS server in the GetMap URL.</td>
</tr>
<tr>
<td>getFeatureInfoParameters</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Additional parameters to pass to the WMS server in the GetFeatureInfo URL.</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> If true, WebMapServiceImageryProvider#pickFeatures will invoke the GetFeatureInfo operation on the WMS server and return the features included in the response. If false, WebMapServiceImageryProvider#pickFeatures will immediately return undefined (indicating no pickable features) without communicating with the server. Set this property to false if you know your WMS server does not support GetFeatureInfo or if you don't want this provider's features to be pickable. Note that this can be dynamically overridden by modifying the WebMapServiceImageryProvider#enablePickFeatures property.</td>
</tr>
<tr>
<td>getFeatureInfoFormats</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> The formats in which to try WMS GetFeatureInfo requests.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The rectangle of the layer. <strong>structure: { west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The tiling scheme to use to divide the world into tiles.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The ellipsoid. If the tilingScheme is specified, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> The width of each tile in pixels.</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre="">256</code></td>
<td><code v-pre="">optional</code> The height of each tile in pixels.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> The minimum level-of-detail supported by the imagery provider. Take care when specifying this that the number of tiles at the minimum level is small, such as four or less. A larger number is likely to result in rendering problems.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The maximum level-of-detail supported by the imagery provider, or undefined if there is no limit. If not specified, there is no limit.</td>
</tr>
<tr>
<td>crs</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> CRS specification, for use with WMS specification &gt;= 1.3.0.</td>
</tr>
<tr>
<td>srs</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> SRS specification, for use with WMS specification 1.1.0 or 1.1.1</td>
</tr>
<tr>
<td>credit</td>
<td>Credit| String</td>
<td></td>
<td><code v-pre="">optional</code> A credit for the data source, which is displayed on the canvas.</td>
</tr>
<tr>
<td>subdomains</td>
<td>String| Array</td>
<td><code v-pre="">'abc'</code></td>
<td><code v-pre="">optional</code> The subdomains to use for the {s} placeholder in the URL template. If this parameter is a single string, each character in the string is a subdomain. If it is an array, each element in the array is a subdomain.</td>
</tr>
<tr>
<td>clock</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Clock instance that is used when determining the value for the time dimension. Required when options.times is specified.</td>
</tr>
<tr>
<td>times</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> TimeIntervalCollection with its data property being an object containing time dynamic dimension and their values.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/WebMapServiceImageryProvider.html">WebMapServiceImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the imagery provider encounters an asynchronous error.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Triggers when the provider is ready for use.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          url: 'http://geoserver.nationalmap.nicta.com.au/geotopo_250k/ows',
          layers: 'Hydrography:bores',
          parameters: {
            transparent : true,
            format : 'image/png'
          },
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      mounted () {
        this.$refs.wms.createPromise.then(( {Cesium, viewer, cesiumObject})=>{
          viewer.camera.setView({
            destination: Cesium.Rectangle.fromDegrees(114.591, -45.837, 148.970, -5.730)
          })
        })
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          this.cesiumInstance = cesiumInstance
        }
      }
    }
  </script>